:root {
    --backdrop-layer: -1;
    --default-layer: 0;
    --component-layer: 0;
    --site-ui-layer: -1;
}


.site-bar {
    position: relative;
    left: 0;
    top: 0;
    height: 56px;
    width: 100%;
    background-color: #000;
}


.site-bar-label {
    position: relative;
    color: #fff;
    font-size: 24px;
    font-family: Inter, system-ui, -apple-system, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, 'Noto Sans',
