.root {
position: relative;
align-items: flex-start;
background-color: #ffffff;

margin: auto;
overflow: auto;
min-height: 550px;
max-height: 4031px;
height: 100%;
}


.site-container{
  text-align: center;
  max-width: 750px;
  margin: 30px auto;
  overflow: auto;
  min-height: 550px;
  border: 10px solid rgba(250, 213, 6, 0.315);
  padding: 30px;
  border-radius: 5px;
}

.coverImage {
  position: relative;
  left: 0;
  top: 56px;
  width: 1440px;
  height: 502px;
  background-color: #ededed;
}
.site-bar {
  position: relative;
  left: 0;
  top: 0;
  height: 56px;
  width: 100%;
  height: 100%;
  background-color: #000;
}

